@import url(https://fonts.googleapis.com/css?family=Baloo+2:400,500,600,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  font-family: 'Baloo 2';
}

button {
  font-family: 'Baloo 2';
  font-weight: 900;
}

.navbar * {
  font-weight: 900;
}

.home-page {
  -webkit-animation-name: home-page-transitions;
          animation-name: home-page-transitions;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  background-size: cover;
  background-position: center;
  text-shadow: 3px 3px black;
}

.home-page h1, .home-page h2 {
  border-radius: 10px;
  background-color: rgba(0, 0,0, 0.5);
}

@-webkit-keyframes home-page-transitions {
  0%   {background-image: url(/static/media/homePageBackground-1.19647afd.jpg)}
  25%  {background-image: url(/static/media/homePageBackground-2.33d77061.jpg)}
  50%  {background-image: url(/static/media/homePageBackground-3.eef9e651.jpg)}
  75%  {background-image: url(/static/media/homePageBackground-4.ddad8f44.jpg)}
  100% {background-image: url(/static/media/homePageBackground-1.19647afd.jpg)}
}

@keyframes home-page-transitions {
  0%   {background-image: url(/static/media/homePageBackground-1.19647afd.jpg)}
  25%  {background-image: url(/static/media/homePageBackground-2.33d77061.jpg)}
  50%  {background-image: url(/static/media/homePageBackground-3.eef9e651.jpg)}
  75%  {background-image: url(/static/media/homePageBackground-4.ddad8f44.jpg)}
  100% {background-image: url(/static/media/homePageBackground-1.19647afd.jpg)}
}
